import React, { Fragment } from "react";
import { Typography, Image, Carousel } from 'antd';
import { v4 as uuidv4 } from 'uuid';
import {PlayCircleOutlined} from '@ant-design/icons';
const { Title, Paragraph, Text } = Typography;


class Education extends React.Component{
    constructor(props) {
        super(props)
    
        this.state = {
            main_title: "",
            eduContent: [],
            img:[]
        }
    }

    componentDidUpdate(prevProps){
        if (this.props.data !== prevProps.data) {
            const {data, img} = this.props;
            this.setState({
                main_title:data.main_title, 
                eduContent:Object.values(data.content),
                img:img}, () =>{}
            )
        }
    }

    componentDidMount(){
        const {data, img} = this.props;
            this.setState({
                main_title:data.main_title, 
                eduContent:Object.values(data.content),
                img:img}, () =>{}
            )
    }

    forAchivementLi = card =>{
        const liCard = (
            typeof card == 'object'?
            <li key={uuidv4()}>
                {card.msg} &emsp;
                <a href={card.url} target="_blank"><PlayCircleOutlined/></a>
            </li> :
            <li key={uuidv4()}>
                {card}
            </li>
        )

        return(
            <Fragment>
                {liCard}
            </Fragment>
        )
    }

    forEducationCard = card => {
        let liCard = Object.values(card.achivement);
        const liChildCard = liCard.map(this.forAchivementLi);
        const eduCard = (
            <Paragraph key={uuidv4()}>
                <Title level={5}>
                    <a href={card.schoolurl} target="_blank">
                        <Image src={this.state.img[card.logo].default} width={30}/>
                    </a>
                    &ensp;
                    <span style={{verticalAlign:'top'}}>{card.course} &emsp;</span><br/>
                    <span style={{fontStyle:'italic', fontSize:'10px', verticalAlign:'80%'}}>
                    &emsp;&emsp;&emsp;&emsp;{card.schoolname}&emsp;&emsp;&emsp;&emsp;&emsp;
                        </span>
                    <span style={{fontStyle:'italic', fontSize:'10px', verticalAlign:'80%'}}>{card.duration}</span>
                </Title>
                <Text>
                        {card.description}
                </Text><br/>
                <br/>
                <Title level={5} style={{fontSize:'14px', fontStyle:'italic'}}>{card.achivement_title}</Title>
                <ul>
                    {liChildCard}
                </ul>
                <hr class="rounded"/>        
            </Paragraph>
        )

        return(
            <Fragment>
                {eduCard}
            </Fragment>
        )
    }
    
    render(){

        const educationCard = this.state.eduContent.map(this.forEducationCard);
        return(
        <Typography 
                style={{width:'35vw', 
                textAlign:'justify', 
                marginLeft:'10vw'}}>
            <Title style={{fontSize:'50px'}}>{this.state.main_title}</Title>
            <Carousel swipeToSlide draggable adaptiveHeight={true} pauseOnHover={true} autoplay autoplaySpeed={5000}>
                {educationCard}
            </Carousel>
        </Typography>
        )
    }
}

export default Education;