import React from "react";
import { Typography, Avatar, Image } from 'antd';
import Skillset from '../component/skillset';

const { Title, Paragraph } = Typography;


class IntroContent extends React.Component{    
    
    render()
        {
            const {home, sk, lan, img} = this.props;
            return(
            <Typography 
                style={{
                        width:'35vw', 
                        textAlign:'justify', 
                        marginLeft:'10vw'}}>
                <Title style={{fontSize:'50px'}}>
                    <Avatar size={128} src={<Image src={img[home.avatar].default}/>}/>
                    &ensp;
                    {home.main_name}
                </Title>
                <Paragraph style={{fontSize:'16px'}}>
                    {home.introduction}
                </Paragraph>
                <Skillset skk={sk} llan={lan}/>
            </Typography>
            )
        }
}

export default IntroContent;