import React from "react";
import { BrowserRouter, Route, Switch} from "react-router-dom";
import App from '../App';
import Myresume from '../component/resume';


const Router = () => (
    <BrowserRouter >
        <Switch>
            <Route exact path="/" component= {App} />
            <Route exact path='/my-resume/:lang' component={Myresume} />
        </Switch>
    </BrowserRouter>
);

export default Router;