import React, { Fragment } from "react";
import { Typography, Image, Carousel } from 'antd';
import { v4 as uuidv4 } from 'uuid';
import {PlayCircleOutlined} from '@ant-design/icons';
const { Title, Paragraph, Text } = Typography;

class Experiences extends React.Component{

    constructor(props) {
        super(props)
    
        this.state = {
             main_title: "",
             expContent: [],
             img:[]
        }
    }
    

    componentDidUpdate(prevProps){
        if (this.props.data !== prevProps.data) {
            const {data, img} = this.props;
            this.setState({
                main_title:data.main_title, 
                expContent:Object.values(data.content),
                img:img}, () =>{}
            )
        }
    }

    componentDidMount(){
        const {data, img} = this.props;
            this.setState({
                main_title:data.main_title, 
                expContent:Object.values(data.content),
                img:img}, () =>{}
            )
    }

    forSummaryLi = card =>{
        const liCard = (
            typeof card == 'object'?
            <li key={uuidv4()}>
                {card.msg} &emsp;
                <a href={card.url} target="_blank"><PlayCircleOutlined/></a>
            </li> :
            <li key={uuidv4()}>
                {card}
            </li>
        )

        return(
            <Fragment>
                {liCard}
            </Fragment>
        )
    }

    forExperienceCard = card =>{
        let liCard = Object.values(card.summary)
        const liChildCard = liCard.map(this.forSummaryLi);
        const experienceCard = (
            <Paragraph key={uuidv4()}>
                <Title level={5}>
                    <a href={card.companyurl} target="_blank">
                        <Image src={this.state.img[card.logo].default} width={30}/>
                    </a>
                    &ensp;
                    <span style={{verticalAlign:'top'}}>{card.position} &emsp;</span>
                    <span style={{fontStyle:'italic', fontSize:'10px', verticalAlign:'80%'}}>{card.duration}</span>
                </Title>
                <Text>
                        {card.introduction}
                </Text><br/>
                <br/>
                <Title level={5} style={{fontSize:'14px', fontStyle:'italic'}}>{card.summary_title}</Title>
                <ul>
                    {liChildCard}
                </ul>
                <hr class="rounded"/>        
            </Paragraph>
        )

        return(
            <Fragment>
                {experienceCard}
            </Fragment>
        )
    }

    render(){
        const expCards = this.state.expContent.map(this.forExperienceCard);
        return(
        <Typography 
                style={{width:'35vw', 
                        textAlign:'justify', 
                        marginLeft:'10vw'}}>
            <Title style={{fontSize:'50px'}}>{this.state.main_title}</Title>
            <Carousel swipeToSlide draggable adaptiveHeight={true} pauseOnHover={true} autoplay autoplaySpeed={5000}>
                {expCards}
            </Carousel>
        </Typography>
        )
    }
}

export default Experiences;