import React, { Fragment } from "react";
import { Card, Tag } from 'antd';


class Skillset extends React.Component{
    constructor(props) {
        super(props)
    
        this.state = {
            key:"Skills",
            tabList:[
                {
                  key: 'Skills',
                  tab: 'Skills',
                },
                {
                  key: 'Languages',
                  tab: 'Languages',
                },
            ]
        }
    }

    onTabChange = (key, type) =>{
        this.setState({ [type]: key });
    }

    forLangMap = card =>{
        const langli = (
            <Tag color='transparent' style={{color:'black'}} >
                {card}
            </Tag>
        )
        return(
            <>
            {langli}
            </>
        )
    }

    forSkillMap = card =>{
        const skillli = (
            <Tag color='transparent' style={{color:'black'}}>
                {card}
            </Tag>
        )

        return (
            <>
            {skillli}
            </>
        )
    }

    getContentList = (key, skills, language) =>{
        const skillCard = skills.map(this.forSkillMap);
        const langCard = language.map(this.forLangMap);
        const contentCard = (
            key == "Skills"?
            <div>
                {skillCard}
            </div>:
            <div>
                {langCard}
            </div>
        );
        return(
            <>
            {contentCard}
            </>
        )
    }

    render(){
        const { skk, llan} = this.props;
        return(
            <Fragment>
                <Card
                    style={{ background:'transparent', color:'black', width: '100%', marginTop:'10vh' }}
                    tabList={this.state.tabList}
                    onTabChange={(key) => { this.onTabChange(key, 'key'); }}>
                </Card>
                {this.getContentList(this.state.key, skk, llan)}
            </Fragment>
        )
    }
    
}

export default Skillset;