import React from "react";
import { Typography, Avatar, Image, Space, Tag } from 'antd';
import { v4 as uuidv4 } from 'uuid';
import {PhoneOutlined, MailOutlined, LinkedinOutlined } from '@ant-design/icons';


const { Title, Paragraph, Text } = Typography;

class Format1 extends React.PureComponent{
    constructor(props) {
        super(props)
    
        this.state = {
             
        }
    }

    forLangMap = card =>{
        const langli = (
            <Tag color='transparent' style={{color:'black'}} >
                {card}
            </Tag>
        )
        return(
            <>
            {langli}
            </>
        )
    }

    forSkillMap = card =>{
        const skillli = (
            <Tag color='transparent' style={{color:'black'}}>
                {card}
            </Tag>
        )

        return (
            <>
            {skillli}
            </>
        )
    }

    forAchivementLi = card =>{
        const liacvCard = (
            typeof card == 'object'?
            <li key={uuidv4()}>
                {card.msg}
            </li> :
            <li key={uuidv4()}>
                {card}
            </li>
        )

        return(
            <>
                {liacvCard}
            </>
        )
    }

    forEducationCard = (card, i, row)=> {
        let liCard = Object.values(card.achivement);
        const liChildCard = liCard.map(this.forAchivementLi);
        const eduCard = (
            <Paragraph key={uuidv4()}>
                <Title level={5}>
                    <span>{card.schoolname}</span><br/>
                    <span style={{fontStyle:'italic', fontSize:'10px'}}>{card.course} &emsp;&emsp;</span>
                    <span style={{fontStyle:'italic', fontSize:'10px'}}>{card.duration}</span>
                </Title>
                <Text>
                        {card.description}
                </Text><br/>
                <br/>
                <ul>
                    {liChildCard}
                </ul>
                {i+1 === row.length ? <></>:<hr class="rounded"/>}        
            </Paragraph>
        )

        return(
            <>
                {eduCard}
            </>
        )
    }

    forSummaryLi = card =>{
        const liCard = (
            typeof card == 'object'?
            <li key={uuidv4()}>
                {card.msg}
            </li> :
            <li key={uuidv4()}>
                {card}
            </li>
        )

        return(
            <>
                {liCard}
            </>
        )
    }

    forExperienceCard = (card, i, row) =>{
        let liCard = Object.values(card.summary)
        const childCardli = liCard.map(this.forSummaryLi);
        const expCard = (
            <Paragraph key={uuidv4()}>
                <Title level={5}>
                    <span>{card.position}</span><br/>
                    <span style={{fontStyle:'italic', fontSize:'10px'}}>{card.companyname}&emsp;&emsp;</span>
                    <span style={{fontStyle:'italic', fontSize:'10px'}}>{card.duration}</span>
                </Title>
                <Text>
                        {card.introduction}
                </Text><br/>
                <br/>
                <ul>
                    {childCardli}
                </ul>
                {i+1 === row.length ? <></>:<hr class="rounded"/>}       
            </Paragraph>
        )
    
        return(
            <>
                {expCard}
            </>
        )}

    render(){
        const {images, infos, languages, lang } = this.props;
        const experiences = Object.values(languages[lang].experience.content);
        const homes = languages[lang].home;
        const skillCard = languages[lang].skill.map(this.forSkillMap);
        const langCard = languages[lang].language.map(this.forLangMap);
        const educationCard = Object.values(languages[lang].education.content).map(this.forEducationCard);
        const experiencesCard = experiences.map(this.forExperienceCard);
        return(
            <>
            <div id='myresumecontent'>
                <div id='header' style={{ display:'flex', alignItems: 'center',
                    minHeight: '35vh', minWidth:'70vw', overflow:'hidden', background:'white'}}>
                        <Space direction='horizontal' size={0} style={{marginLeft:'auto', marginRight:'auto', textAlign:'justify'}}>
                            <Avatar size={160} src={<Image src={images[homes.avatar].default}/>}
                            style={{ border:'2px solid black'}}/>
                            <Title level={2} style={{
                                backgroundColor:'black', color:'white', maxHeight:'20vh',
                                marginBottom:'0em', border:'4px solid black', paddingTop:'0.65em', paddingBottom:'0.8em',
                                marginLeft:'-2em', paddingLeft:'2.2em', paddingRight:'1em', maxWidth:'30vw'}}>
                                {languages[lang].home.main_name}
                                <Paragraph style={{color:'white', fontSize:'10px'}}>
                                {experiences[0].position}
                                </Paragraph>
                                <div style={{fontSize:'12px'}}>
                                    <PhoneOutlined />&emsp;
                                    <span>{infos[lang].contact}</span>
                                </div>
                                <div style={{fontSize:'12px'}}>
                                    <MailOutlined />&emsp;
                                    <span>{infos[lang].email}</span>
                                </div>
                                <div style={{fontSize:'12px'}}>
                                    <LinkedinOutlined />&emsp;
                                    <span>{infos[lang].social_media.linkedin}</span>
                                </div>
                                
                            </Title>
                        </Space>
                        
                </div>
                <div style={{display:'flex'}}>
                    <div id='leftdiv' style={{position:'flex', alignItems:'center',
                        minHeight: '65vh', minWidth:'35vw', background:'white', borderRight:'2px solid black',
                        textAlign:'center', float:'left'}}>
                        
                        <Title level={5} style={{
                                    backgroundColor:'black', color:'white', marginBottom:'0em', maxWidth:'70%',
                                    marginLeft:'auto', marginRight:'auto', paddingTop:'0.5em', paddingBottom:'0.5em'}}>
                                About Me:
                        </Title>
                        <span style={{display:'inline-block', maxWidth:'24vw', textAlign:'justify', fontSize:'12px', 
                        marginTop:'0.5em', marginBottom:'0.5em'}}>
                            {homes.introduction}
                        </span>
                        <Title level={5} style={{
                                    backgroundColor:'black', color:'white', marginBottom:'0em', maxWidth:'70%',
                                    marginLeft:'auto', marginRight:'auto', paddingTop:'0.5em', paddingBottom:'0.5em'}}>
                                Educations:
                        </Title>
                        <span style={{display:'inline-block', maxWidth:'24vw', textAlign:'justify', fontSize:'12px', 
                        marginTop:'0.5em', marginBottom:'0.5em'}}>
                            {educationCard}
                        </span >

                        <Title level={5} style={{
                                    backgroundColor:'black', color:'white', marginBottom:'0em', maxWidth:'70%',
                                    marginLeft:'auto', marginRight:'auto', paddingTop:'0.5em', paddingBottom:'0.5em'}}>
                                Skills:
                        </Title>
                        <span style={{display:'inline-block', maxWidth:'24vw', textAlign:'justify', fontSize:'12px', 
                        marginTop:'0.5em', marginBottom:'0.5em'}}>
                            {skillCard}
                        </span>

                        <Title level={5} style={{
                                    backgroundColor:'black', color:'white', marginBottom:'0em', maxWidth:'70%',
                                    marginLeft:'auto', marginRight:'auto', paddingTop:'0.5em', paddingBottom:'0.5em'}}>
                                Languages:
                        </Title>
                        <span style={{display:'inline-block', maxWidth:'24vw', textAlign:'justify', fontSize:'12px', 
                        marginTop:'0.5em', marginBottom:'0.5em'}}>
                            {langCard}
                        </span>

                    </div>
                    <div id='rightdiv' 
                    style={{minHeight: '65vh', background:'white', textAlign:'center', overflow:'hidden',
                            minWidth:'35vw', borderLeft:'2px solid black'}}>
                        <Title level={5} style={{
                                    backgroundColor:'black', color:'white', marginBottom:'0em', maxWidth:'70%',
                                    marginLeft:'auto', marginRight:'auto', paddingTop:'0.5em', paddingBottom:'0.5em'}}>
                                Experiences:
                        </Title>
                        <span style={{display:'inline-block', maxWidth:'24vw', textAlign:'justify', fontSize:'12px', 
                        marginTop:'0.5em', marginBottom:'0.5em'}}>
                        {experiencesCard}
                        </span>
                    </div>
                </div>
                <div id='footer' style={{display:'flex', alignItems: 'center',
                    minHeight: '10vh', minWidth:'70vw', background:'white', textAlign:'center'}}>
                        <div style={{
                            height:'5vh', width:'75%', background:'black', marginLeft:'auto', marginRight:'auto',
                            borderBottomLeftRadius:'20px', borderBottomRightRadius:'20px'}} />
                </div>
            </div>
            </>
        )
    }
    
}

export default Format1;