import React, { Fragment } from "react";
import { Typography, Image, Carousel } from 'antd';
import { v4 as uuidv4 } from 'uuid';
import {PlayCircleOutlined} from '@ant-design/icons';
const { Title, Paragraph, Text } = Typography;

class Project extends React.Component{
    constructor(props) {
        super(props)
    
        this.state = {
            main_title: "",
            prjContent: [],
            img:[]
        }
    }

    componentDidUpdate(prevProps){
        if (this.props.data !== prevProps.data) {
            const {data, img} = this.props;
            this.setState({
                main_title:data.main_title, 
                prjContent:Object.values(data.content),
                img:img}, () =>{}
            )
        }
    }

    componentDidMount(){
        const {data, img} = this.props;
            this.setState({
                main_title:data.main_title, 
                prjContent:Object.values(data.content),
                img:img}, () =>{}
            )
    }

    forSummaryLi = card =>{
        const liCard = (
            typeof card == 'object'?
            <li key={uuidv4()}>
                {card.msg} &emsp;
                <a href={card.url} target="_blank"><PlayCircleOutlined/></a>
            </li> :
            <li key={uuidv4()}>
                {card}
            </li>
        )

        return(
            <Fragment>
                {liCard}
            </Fragment>
        )
    }

    forProjectLi = card => {
        let liCard = Object.values(card.summary)
        const liChildCard = liCard.map(this.forSummaryLi);
        const prjCard = (
            <Paragraph key={uuidv4()}>
                <Title level={4}>
                    {card.title}
                </Title>
                <Text>
                        {card.description}
                </Text><br/>
                <ul>
                    {liChildCard}
                </ul>
                <hr class="rounded"/>        
            </Paragraph>
        )
        return(
            <Fragment>
                {prjCard}
            </Fragment>
        )
    }

    render() {
        const prjCard = this.state.prjContent.map(this.forProjectLi);
        return (
        <Typography 
            style={{width:'35vw', 
            textAlign:'justify', 
            marginLeft:'10vw'}}>
            <Title style={{fontSize:'50px'}}>{this.state.main_title}</Title>
            {prjCard}
        </Typography>
        );
    }
    
}

export default Project;