import React from 'react';
import { Layout, Menu } from 'antd';
import {
    HomeOutlined,
    LinkedinOutlined,
    FacebookOutlined,
    GithubOutlined
} from '@ant-design/icons';
import IntroContent from '../component/intro'
import Experiences from '../component/experiences';
import Education from '../component/education';
import Project from '../component/project';
import { BrowserRouter as Router, Route, Link} from "react-router-dom";
import info from '../social_media/info.json';
import lang from '../languages/lang.json';

const { Header, Content, Sider } = Layout;
const images = importAll(require.context('../img', false, /\.(png|jpe?g|svg)$/));
const infos = JSON.parse(JSON.stringify(info));
const infokeys = Object.keys(infos);
const languages = JSON.parse(JSON.stringify(lang));
const langkeys = Object.keys(languages);


class MainPageLayout extends React.Component{
  constructor(props) {
    super(props)
  
        this.state = {
          lang:languages[langkeys[0]],
          home:languages[langkeys[0]].home,
          social_media:infos[infokeys[0]].social_media,
          mainpage:infos[infokeys[0]].mainpage,
          experience:languages[langkeys[0]].experience,
          education:languages[langkeys[0]].education,
          project:languages[langkeys[0]].project,
          skill:languages[langkeys[0]].skill,
          language:languages[langkeys[0]].language,
          img:images
        }
      }

      setBackground = () =>{
        document.body.style.background = `url(${images[infos[infokeys[0]].background].default})`;
        document.body.style.backgroundRepeat = 'no-repeat';
        document.body.style.backgroundSize = 'cover';
        document.body.style.backgroundAttachment = 'fixed';
      }
    
      render() {
        this.setBackground();
        return (
          <Router >
            <Layout style={{ minHeight: '100vh', background:'transparent', color:'black'}}>
              <Header className="header" 
                style={{background:'transparent', color:'black'}}>
                <Menu mode="horizontal"
                style={{
                  background:'transparent', color:'black', borderBottom:'0px solid transparent', 
                  marginLeft:"65%"}}>
                    <Menu.Item key="exp1"> 
                      {this.state.mainpage.experiences}
                      <Link to='/experiences'/>
                      </Menu.Item>
                    <Menu.Item key="edu2">
                    {this.state.mainpage.educations}
                      <Link to='/educations'/>
                    </Menu.Item>
                    <Menu.Item key="prj3"> 
                    {this.state.mainpage.projects}
                      <Link to='/projects'/>
                    </Menu.Item>
                    <Menu.Item key="rsm4">
                    {this.state.mainpage.resume}
                      <Link to='/my-resume/en' target="_blank"/>
                    </Menu.Item>
                    <Menu.Item key="blg5">
                    {this.state.mainpage.blog}
                    </Menu.Item>
                </Menu>
              </Header>
              <Layout style={{background:'transparent', color:'black' }}>
                <Sider width={100} className="site-layout-background" 
                style={{background:'transparent', color:'black', position:'fixed' }}>
                  <Menu
                    mode="inline"
                    style={{ height: '100%', borderRight: 0, background:'transparent', color:'black' }}
                  >
                       <Menu.Item  key="hme1" icon={<HomeOutlined />} >
                         <Link to='/'/>
                       </Menu.Item>
                       <Menu.Item  key="lnk2" icon={<LinkedinOutlined />}>
                          <a href={this.state.social_media.linkedin} target="_blank"/>
                        </Menu.Item> 
                       <Menu.Item  key="fbc3" icon={<FacebookOutlined />}> 
                        <a href={this.state.social_media.facebook} target="_blank"/>
                       </Menu.Item>
                       <Menu.Item  key="git4" icon={<GithubOutlined />} > 
                        <a href={this.state.social_media.github} target="_blank"/>
                       </Menu.Item>
                  </Menu>
                </Sider>
                <Layout style={{ padding: '0 24px 24px', background:'transparent', color:'black'}} >
                  <Content
                    className="site-layout-background"
                    style={{
                      padding: 24,
                      margin: 0,
                      minHeight: 280,
                      textAlign: 'left'
                    }}>
                      
                        <Route exact path="/">
                          <IntroContent 
                          home={this.state.home} sk={this.state.skill} lan={this.state.language}
                          img={this.state.img}/>
                        </Route>
                        <Route exact path="/experiences">
                          <Experiences data={this.state.experience} img={this.state.img}/>
                        </Route>
                        <Route exact path="/educations">
                          <Education data={this.state.education} img={this.state.img}/>
                        </Route>
                        <Route exact path="/projects">
                          <Project data={this.state.project} img={this.state.img}/>
                        </Route>
                  </Content>
                </Layout>
              </Layout>
            </Layout>
          </Router>
          )
      }
}

function importAll(r) {
  let images = {};
  r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
  return images;
}

export default MainPageLayout;
