import React from "react";
import { Affix, Button } from 'antd';
import info from '../social_media/info.json';
import lang from '../languages/lang.json';
import { DownloadOutlined } from '@ant-design/icons';
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import Format1 from "../resumeformat/format_1";


const images = importAll(require.context('../img', false, /\.(png|jpe?g|svg)$/));
const infos = JSON.parse(JSON.stringify(info));
const languages = JSON.parse(JSON.stringify(lang));

class Myresume extends React.Component{
    constructor(props) {
        super(props)
    
        this.state = {
        }
    }

    downloadPDF = () =>{
        html2canvas(document.querySelector('#myresumecontent'),
        { scale: 5, quality:4 }
            ).then(canvas => {
                const pdf = new jsPDF('p', 'mm', 'a4');
                pdf.addImage({imageData:canvas.toDataURL('image/jpg'), format:'JPEG', x:0, y:0, w:211, h:298, compression:'FAST'})
                pdf.save('myresume.pdf');
            });
    }

    render(){
        const lang = this.props.match.params.lang;
        return(
            <>
            <Affix style={{position:'absolute', zIndex:'2', right:'5em', top:'50%'}}>
                <Button shape='circle' size='large' icon={<DownloadOutlined />} onClick={this.downloadPDF}
                    style={{border:'3px solid black'}}/>
            </Affix>
            <div style={{paddingLeft:'15vw', paddingRight:'15vw', marginLeft:'auto', marginRight:'auto'}}>
                <Format1 images={images} infos={infos} languages={languages} lang={lang} />
            </div>
            </>
        )
    }
    
}

function importAll(r) {
    let images = {};
    r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
    return images;
  }

export default Myresume;